const getAppVersion = async () => {
    if (top.cordova) { //  Cordova App 
        const appVersion = await top.cordova.getAppVersion.getVersionNumber()
        return `v${appVersion}`
    } else { // 
        return "v0.9.99-"
    }
}

export default getAppVersion
