
/**
 *  shell  shell 
 * @author Xmader
 * @param {string | string[]} cmd 
 * @returns {Promise<{ exitStatus: number; output: string; }>}
 */
export const shellExecPromise = (cmd) => {
    return new Promise((resolve, reject) => {
        window.ShellExec.exec(cmd, (res) => {
            if (res.exitStatus == 0) {
                resolve(res)
            } else {
                reject(res)
            }
        })
    })
}

/**
 * PID
 * @param {string} name 
 */
export const getPID = async (name) => {
    try {
        const { output } = await shellExecPromise(["sh", "-c", `ps | grep $(whoami) | grep ${name}`]) // AndroidpsGNU/Linux

        return output.split(/\n|\r\n/g).map(x => {
            const r = x.match(/\s+(\d+)/)
            return r && +r[1]
        }).filter(x => !!x)

    } catch (e) {
        return []
    }
}
