
<template>
    <xm-container>

        <xm-blank></xm-blank>

        <md-card>
            <md-card-header>
                <md-card-header-text>
                    <div class="md-title">Revolter</div>
                    <div class="md-subhead">{{appVersion}}</div>
                </md-card-header-text>

                <md-card-media md-medium>
                    <img :src="'./icon.png'">
                </md-card-media>
            </md-card-header>

            <md-card-actions>
                <md-button v-if="update.checking" disabled></md-button>
                <md-button v-else @click="doCheckUpdate"></md-button>
            </md-card-actions>
        </md-card>

        <xm-blank></xm-blank>

        <md-card v-if="update.available !== null">
            <md-card-header>
                <template v-if="update.available">
                    <div class="md-title">!</div>
                    <div class="md-subhead">
                        : {{appVersion}}
                        <br />
                        : {{update.newVersion}}
                    </div>
                </template>

                <template v-else>
                    <div class="md-title"></div>
                    <div class="md-subhead"> <em>{{appVersion}}</em> </div>
                </template>
            </md-card-header>

            <md-card-actions>
                <md-button class="md-warn" v-if="update.available" href="https://github.com/Xmader/Revolter/releases/latest" target="_blank"></md-button>
                <md-button @click="update.available = null">{{update.available ? "" : ""}}</md-button>
            </md-card-actions>
        </md-card>

        <xm-blank></xm-blank>

        <md-card>
            <md-card-header>
                <md-layout md-align="center">
                    <div class="md-subhead" style="text-align:center;">
                        Revolter <br />
                        Copyright (c) 2018 Xmader. <br />
                        <!-- License: MIT <br /> -->
                        <br />
                        Revolter  Android App , , 
                        <br />
                        Revolterbug, 
                    </div>
                </md-layout>
            </md-card-header>

            <md-card-actions>
                <md-button class="md-warn" href="https://github.com/Xmader/Revolter/issues" target="_blank">BUG </md-button>
                <md-button href="https://github.com/Xmader/Revolter/" target="_blank"></md-button>
            </md-card-actions>
        </md-card>

        <xm-blank></xm-blank>

        <md-card v-if="false">
            <!-- ,  -->
            <md-card-header>
                <div class="md-subhead">Revolter </div>
            </md-card-header>

            <md-layout md-align="center">
                <div class="md-subhead">
                    Vue.js <br />
                    Vue Material <br />
                    Cordova <br />
                </div>
            </md-layout>

            <xm-blank></xm-blank>
        </md-card>

    </xm-container>
</template>

<script>

import getAppVersion from "../util/get-app-version.js"
import { getLatestVersionNumber, compareVersionNumbers } from "../util/check-update.js"

import xmBlank from "./xmBlank.vue"
import xmContainer from "./xmContainer.vue"

export default {
    components: {
        xmBlank,
        xmContainer
    },
    data() {
        return ({
            appVersion: "",
            update: {
                available: null,
                checking: false,
                newVersion: ""
            },
        })
    },
    methods: {
        async doCheckUpdate() {
            this.update.checking = true

            const newVersion = await getLatestVersionNumber()
            const available = compareVersionNumbers(this.appVersion, newVersion)

            this.update.available = available
            this.update.newVersion = available ? newVersion : ""

            this.update.checking = false
        }
    },
    async mounted() {
        this.appVersion = await getAppVersion()
    },
}
</script>
